      },
      /**
       * 数据处理
       */
      nullTreating(row, column, cellValue) {
        if (!cellValue) {
          return '--'
        } else {
          return cellValue
        }
      },
      /**
       * [orderTypeFormatter 格式化订单类型]
       */
      orderTypeFormatter(row, column, cellValue) {
        if (cellValue === 1) {
          return '及时送'
        } else if (cellValue === 2) {
          return '预约送'
        } else if (cellValue === 3) {
          return '自提单'
        }
      },
      /**
       * [changeLogisticsCompany description]
       * @param  {[type]} _val [description]
       * @return {[type]}      [description]
       */
      changeLogisticsCompany(_val) {
        let logisticsCompanyCode = _val
        this.logisticsCompanyId = logisticsCompanyCode
        let logisticsCompan = this.logisticsCompanyOptList.filter(item => {
          return item.logisticsCompanyId === logisticsCompanyCode
        })[0]
        this.logisticsId = logisticsCompan ? logisticsCompan.logisticsId : ''
      },
      /**
       * [orderDelivery 获取配改订单所在门店的已经开通的配送公司]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      orderDelivery(_row) {
        this.orderNo = _row.orderId || ''
        let storeNo = _row.storeNo
        let logisticsCompanyCode = _row.logisticsCompanyCode ? parseInt(_row.logisticsCompanyCode) : ''

        // 初始化将要用到的参数
        this.logisticsCompanyId = ''
        this.logisticsId = ''
        this.logisticsCompanyOptList = []

        this.$http.post(orderMgtUrl.getMobileLogisticsList, {
          storeNo: storeNo,
          DIFFCITY: true
        }).then(res => {
          if (res.code === 0) {
            if (res.data && res.data.length) {
              this.dialogChangeOrderVisible = true
              this.logisticsCompanyId = logisticsCompanyCode
              let logisticsCompan = res.data.filter(item => {
                return item.logisticsCompanyId === logisticsCompanyCode
              })[0]
              this.logisticsId = logisticsCompan ? logisticsCompan.logisticsId : ''
              this.logisticsCompanyOptList = res.data
            } else {
              this.$notify.warning({
                title: '提示',
                message: '未获取到可派单的配送公司'
              })
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [orderDeliveryHandel 确认修改配方式]
       * @return {[type]} [description]
       */
      orderDeliveryHandel() {
        if (!this.logisticsCompanyId) {
          this.$message("请选择配送方式")
          return
        }
        let _params = {
          logisticsId: this.logisticsId,
          orderNo: this.orderNo,
          logisticsCompanyId: this.logisticsCompanyId,
          DIFFCITY: true
        }
        this.isDisabled = true
        this.$http.post(orderMgtUrl.updateOrderLogistics, _params).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if (res.code === 0) {
            this.$message.success("派单成功")
            this.dialogChangeOrderVisible = false
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      emptyFormatter(row, column, cellValue) {
        return cellValue && cellValue != "" ? cellValue : '--'
      },
      showUploadHandel() {
        if(this.$refs.batchOrders) {
          let _params = {
            title: '通过Excel批量核销',
            from: 'BLX'
          }
          this.$refs.batchOrders.setDefaultMsg(_params)
        }
      },
      /**
       * @description: 部分退款
       */
      showRefundDialog(row) {
        this.refundDialogVisible = true
        this.skuNum = 0
        this.totalAmount = 0
        this.totalNum = 0
        this.reasonItemMsg = {}
        this.getToRefundMsg(row.orderId, row.storeNo, row.orderStatus)
        this.getReason()
      },
      getToRefundMsg(orderNo, storeNo, orderStatus) {
        this.refundPartLoading = true
        this.$http.post(orderMgtUrl.toRefund, {orderNo, storeNo, DIFFCITY: true}).then(res => {
          this.refundPartLoading = false
          if(res.code === 0) {
            if(res.data) {
              this.orderBaseMsg.orderNo = res.data.orderNo || ''
              this.orderBaseMsg.goodsSaleAmount = res.data.goodsSaleAmount || ''
              this.orderBaseMsg.orderStatus = parseInt(orderStatus)
              let refundVoListArr = res.data.refundVoList && res.data.refundVoList.length ? res.data.refundVoList : []
              refundVoListArr.forEach(item => {
                item.refundPrice = ''
                item.refundQuantityVal = 0
                item.reasonItemMsg = {}
                item.refundQualityItemMsg = {}
              })
              this.orderBaseMsg.allSkuNum = refundVoListArr.length // 整单退款中份数
              let allQuantityNum = 0
              refundVoListArr.forEach(item => {
                allQuantityNum = allQuantityNum + parseInt(item.quantity)
              })
              this.orderBaseMsg.allQuantityNum = allQuantityNum
              this.orderBaseMsg.allSkuNum = refundVoListArr.length
              this.refundVoList = refundVoListArr
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.refundPartLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      getReason() {
        this.$http.post(commonReqUrl.listByCommonCodes, ['MidReason', 'QualityReason'], true).then(res => {
          if(res.code === 0) {
            this.refundReasonList = res.data && res.data.MidReason && res.data.MidReason.length ? res.data.MidReason : []
            this.refundQualityReasonList = res.data && res.data.QualityReason && res.data.QualityReason.length ? res.data.QualityReason : []
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("【提示】获取退款原因：", e.message)
        })
      },
      /**
       * @description: 部分退款
       */
      confirmPartRefundHandel() {
        if(!this.reasonItemMsg || !this.reasonItemMsg.code) {
          this.$message.warning(`请选择退款原因`)
          return
        }
        let itemList = []
        // 未完成订单：待配送code 3,待自提code 4,配送中code 5;
        // 已完成订单：已完成code 6,已退款、已失效、待成团应该不显示退款按钮。因为还没有成单;
        if(this.orderBaseMsg.orderStatus === 6) {
          let refundVoListTemp = this.refundVoList.filter(item => {
            return item.refundPrice
          })
          for(let item of refundVoListTemp) {
            if(!salePriceReg.test(item.refundPrice)) {
              this.$message.warning(`【${item.skuNo}_${item.goodsName}】退款金额为大于0，小数保留小数点后2位的数字`) 
              return
            }else {
              if(parseFloat(item.refundPrice) > parseFloat(item.refundAmount)) {
                this.$message.warning(`【${item.skuNo}_${item.goodsName}】退款金额不能大于可退金额`) 
                return
              }
            }
          }
          itemList = refundVoListTemp.map(item => {
            return {
              id: item.id,
              goodsName: item.goodsName,
              refundPrice: item.refundPrice,
              refundCode: item.reasonItemMsg.code || '',
              refundMsg: item.reasonItemMsg.value || '',
              refundQualityCode: item.refundQualityItemMsg.code || '',
              refundQualityMsg: item.refundQualityItemMsg.value || '',
            }
          })
          if(itemList.length === 0) {
            this.$message.warning(`退款商品明细不能为空(退款金额为大于0，小数保留小数点后2位的数字)`)
            return
          }
        }else if(this.orderBaseMsg.orderStatus === 3 || this.orderBaseMsg.orderStatus === 4 || this.orderBaseMsg.orderStatus === 5) {
          itemList = this.refundVoList.filter(item => {
            return item.refundQuantityVal
          }).map(item => {
            return {
              id: item.id,
              goodsName: item.goodsName,
              refundQuantity: item.refundQuantityVal,
              refundCode: item.reasonItemMsg.code || '',
              refundMsg: item.reasonItemMsg.value || '',
              refundQualityCode: item.refundQualityItemMsg.code || '',
              refundQualityMsg: item.refundQualityItemMsg.value || '',
            }
          })
          if(itemList.length === 0) {
            this.$message.warning(`退款商品明细不能为空(退款数量为大于0的整数)`)
            return
          }
        }
        for(let item of itemList) {
          if(item.refundCode) {
            // 质量问题  必须选择原因类别
            if(item.refundCode == 102 && !item.refundQualityCode) {
              this.$message.warning(`请选择 [${item.goodsName}] 原因类别`)
              return
            }
          }else {
            this.$message.warning(`请选择 [${item.goodsName}] 退款原因`)
            return
          }
        }
        let params = {
          orderNo: this.orderBaseMsg.orderNo,
          refundCode: this.reasonItemMsg.code,
          refundMsg: this.reasonItemMsg.value,
          items: itemList,
          DIFFCITY: true
        }
        this.isDisabled = true
        this.$http.post(orderMgtUrl.doRefund, {...params}).then(res => {
          this.isDisabled = false
          if(res.code === 0) {
            this.refundDialogVisible = false
            this.$message.success("部分退款成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * @description: 输入要退数量发生变化
       * @param {type} 
       * @return: 
       */
      refundQuantityChange() {
        this.skuNum = this.refundVoList.filter(item => {
          return item.refundQuantityVal
        }).length
        let totalNumVal = 0
        this.refundVoList.forEach(item => {
          totalNumVal += parseInt(item.refundQuantityVal)
        })
        this.totalNum = isNaN(totalNumVal) ? '-' : totalNumVal
      },
      refundPriceChange() {
        this.skuNum = this.refundVoList.filter(item => {
          return item.refundPrice
        }).length
        let totalAmountVal = 0
        this.refundVoList.forEach(item => {
          if(item.refundPrice) {
            totalAmountVal += parseFloat(item.refundPrice)
          }
        })
        this.totalAmount = isNaN(totalAmountVal) ? '-' : parseFloat(totalAmountVal).toFixed(2)
      },
      /**
       * @description: 获取所属团长店信息
       * @param {type} 
       * @return: 
       */
      getStoreLeaderList() {
        this.$http.post(orderMgtUrl.getStoreLeaderList, {pageNum: 1, pageSize: -1, DIFFCITY: true}).then(res => {
          if (res.code === 0) {
            let optionsArr = res.data && res.data.result && res.data.result.length ? res.data.result : []
            this.storeLeaderOptions = optionsArr.filter(item => {
              return item.applyStatus === 2 && item.userStatus === 0
            }).map(item => {
              return {
                id: item.id,
                applyStatus: item.applyStatus,
                virtualStoreName: item.virtualStoreName,
                userName: item.userName,
                actualStoreNo: item.actualStoreNo,
                value: item.id,
                label: `${item.id}_${item.virtualStoreName}_${item.userName}`
              }
            })
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取所属团长店信息):", e.message)
        })
      },
      /** 
       * @description: 下单时间范围发生变化
       * @param {type} 
       * @return: 
       */
      datePickerRangeChange(result) {
        this.searchContent.defaultRange = result ? [...result] : result
        this.searchContent.startDate = result ? result[0] : ''
        this.searchContent.endDate = result ? result[1] : ''
        this.doSearchHandel(true)
      },
      /**
       * @description: 显示整单退款弹窗
       */
      showAllRefundDiaog(row) {
        this.allRefundDialogVisible = true
        this.skuNum = 0
        this.totalAmount = 0
        this.totalNum = 0
        this.reasonItemMsg = {}
        this.getToRefundMsg(row.orderId, row.storeNo, row.orderStatus)
        this.getReason()
        this.orderBaseMsg.orderStatusBool = row.orderStatus === 5 && row.orderType === 5 ? true : false // orderStatusBool 记录是不是配送中的整单退款
      },
      reasonMsgChange(rowMsg) {
        this.refundQualityItemMsg = {}
        this.refundVoList.forEach(item => {
          item.reasonItemMsg = {...rowMsg}
          item.refundQualityItemMsg = {}
        })
      },
      refundQualityItemMsgChange(rowMsg) {
        console.log("rowMsg:", rowMsg)
        this.refundVoList.forEach(item => {
          item.refundQualityItemMsg = {...rowMsg}
        })
      },
      reasonItemMsgChange(idx) {
        if(this.refundVoList[idx] && this.refundVoList[idx].refundQualityItemMsg) this.refundVoList[idx].refundQualityItemMsg = {}
      },
      /**
       * @description: 确认整单退款
       */
      confirmAllRefundHandel() {
        // 配送中的整单退款
        if(this.orderBaseMsg.orderStatusBool) {
          this.refundHandelDelivery(this.orderBaseMsg.orderNo)
        }else {
          this.refundHandel(this.orderBaseMsg.orderNo)
        }
      },
      /**
       * [refundHandel 退款]
       * @param  {[type]} row [description]
       * @return {[type]}      [description]
       */
      refundHandel(orderId) {
        if(this.reasonItemMsg && this.reasonItemMsg.code) {
          // 质量问题  必须选择原因类别
          if(this.reasonItemMsg.code == 102 && !this.refundQualityItemMsg.code) {
            this.$message.warning("请选择原因类别")
            return
          }
        }else {
          this.$message.warning("请选择退款原因")
          return
        }
        let params = {
          opType: 3, // 操作类型 1置为过期，2修改门店 3退款 4核销 
          orderIds: [orderId],
          refundCode: this.reasonItemMsg.code,
          refundMsg: this.reasonItemMsg.value,
          refundQualityCode: this.refundQualityItemMsg.code,
          refundQualityMsg: this.refundQualityItemMsg.value,
          DIFFCITY: true
        }
        this.isDisabled = true
        this.$http.post(orderMgtUrl.dealOrder, params).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if (res.code === 0) {
            this.allRefundDialogVisible = false
            this.$message.success("退款成功")
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * @description: 配送类型为快递配送并且订单状态为配送中的时候的退款
       * @param {type} 
       * @return: 
       */
      refundHandelDelivery(orderId) {
        if(this.reasonItemMsg && this.reasonItemMsg.code) {
          // 质量问题  必须选择原因类别
          if(this.reasonItemMsg.code == 102 && !this.refundQualityItemMsg.code) {
            this.$message.warning("请选择原因类别")
            return
          }
        }else {
          this.$message.warning("请选择退款原因")
          return
        }
        let params = {
          orderIds: orderId,
          refundCode: this.reasonItemMsg.code,
          refundMsg: this.reasonItemMsg.value,
          refundQualityCode: this.refundQualityItemMsg.code,
          refundQualityMsg: this.refundQualityItemMsg.value,
          DIFFCITY: true
        }
        this.isDisabled = true
        this.$http.post(orderMgtUrl.completeAndRefund, {...params}).then(res => {
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if (res.code === 0) {
            this.$message.success("退款成功")
            this.allRefundDialogVisible = false
            this.doSearchHandel()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave(to, from, next) {
      from.meta.keepAlive = false;
      next();
    }

  }

</script>



