<style>
.p-t {
  margin-bottom: 10px;
}

.u-btn-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.file-name-wrap {
  width: 200px;
  height: 32px;
  line-height: 32px;
  overflow: hidden;
  border: 1px solid #ececec;
  border-radius: 4px;
  padding: 0 10px;
  position: relative;
  top: 5px;
}

.label-wrap {
  border: 1px solid #ececec;
  border-radius: 4px;
  position: relative;
  top: 5px;
  height: 32px;
  line-height: 32px;
}

.label-wrap > .excel-file-label {
  line-height: 32px;
  cursor: pointer;
  padding: 7px 10px;
}

.label-wrap > .excel-file-label:hover {
  color: #fff;
  background-color: #66b1ff;
  border-radius: 4px;
}
</style>

<template>
  <div>
    <el-dialog :title="title" :visible.sync="isShow" width='450px'>
      <el-form ref="form" label-width="80px">
        <el-form-item label="本地上传">
          <div class="excel-u-d-wrap">
            <div class="u-btn-wrap">
              <div class="file-name-wrap">{{ file.name }}</div>
              <div class="label-wrap">
                <label for="excelFile" class="excel-file-label">
                  <input type="file" id="excelFile" hidden @change="handleFileSelect"/>
                  <span class="u-txt">选择文件</span>
                </label>
              </div>
            </div>
            <div class="d-btn-wrap">
              <span>请按Excel模板格式导入</span>
              <el-button type="text" @click="downloadModel" size="mini" :disabled="isDisabled">下载模板</el-button>
            </div>
          </div>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false" size="mini">取消</el-button>
        <el-button type="primary" @click="doConfirmHandel" size="mini" :disabled="!file.name || conDisabled" :loading="saveLoading" v-if="from === 'SF'">核销</el-button>
        <el-button type="primary" @click="doConfirmHandelBlx" size="mini" :disabled="!file.name || conDisabled" :loading="saveLoading" v-if="from === 'BLX'">核销</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import * as utils from '@/utils/utils'
import exportSheet from '@/utils/exportSheet'
import Storage from '@/utils/localStorage'
const requestUrl = {
  // 操作顺丰订单批量核销处理订单
  dealOrder: '/plateform/a/order/checkOrder',
  // 操作商城订单批量核销处理订单
  cancelUserRoleOrder: '/member/order/deal/cancelUserRoleOrder'
}
export default {
  data () {
    return {
      from: '',
      conDisabled: false, // 防止多次点击确定按钮
      isDisabled: false, // 下载模板按钮防止多次点击
      saveLoading: false,
      title: '通过Excel批量核销',
      isShow: false,
      formLabelWidth: '70px',
      file: {name: ''}, // 导入数据
      // 导入商品
      exportGoods: [],
      tempConfig: {
        orderId: '订单号（必填）'
      },
      // 批量添加商品集合
      list: []
    }
  },
  methods: {
    /**
     * [setDefaultMsg 设置弹框默认属性]
     * @param {[type]} _params [自定义参数]
     */
    setDefaultMsg (_params) {
      this.isShow = true
      if (_params.title) {
        this.title = _params.title
      }
      if(_params.from) {
        this.from = _params.from
      }
      // 清除当前选择的文件信息
      this.file = {name: ''}
      let excelFileDom = document.getElementById('excelFile')
      if(excelFileDom) {
        excelFileDom.value = ""
      }
    },
    /**
     * [downloadModel 下载模板]
     * @return {[type]} [description]
     */
    downloadModel () {
      let cityName = Storage.get("cityName")
      this.isDisabled = true
      let dataArr = [
        {
          orderId: ''
        }
      ]
      exportSheet.exportFileByConfig(dataArr, `${cityName}` + '_批量核销订单模板.xlsx', this.tempConfig)
      this.$notify.success({
        title: '温馨提示',
        message: `${cityName}` + '_批量核销订单模板下载成功'
      })
      setTimeout(() => {
        this.isDisabled = false
      }, 500)
    },
    /**
     * [handleFileSelect 选择文件发生变化]
     * @param  {[type]} e [description]
     * @return {[type]}   [description]
     */
    handleFileSelect (e) {
      // 点击取消不做任何变化
      if (!e.target.files.length) {
        return
      }
      this.file = e.target.files[0] || {name: ''}
      let idx = this.file.name.lastIndexOf(".")
      let strLength = this.file.name.length
      let postf = this.file.name.substring(idx, strLength) // 后缀名

      if (!this.file.name || postf != '.xlsx') {
        this.$notify.warning({
          title: '提示',
          message: '请选择excel文件'
        })
        // 清除当前选择的文件信息
        this.file = {name: ''}
        document.getElementById('excelFile').value = ""
        return
      }

      let items = []
      let parseConfig = {}
      for (let key in this.tempConfig) {
        parseConfig[this.tempConfig[key]] = key
      }
      items = exportSheet.parseToJSON(this.file, parseConfig)
      items.then((result) => {
        // 过滤空行
        let resultVal = result.filter(item => !!item.orderId)
        let listArrTemp = []
        for (let item of resultVal) {
          if (!item.orderId) {
            this.$notify.warning({
              title: '提示',
              message: `请填写订单号`
            })
            // 清除当前选择的文件信息
            this.file = {name: ''}
            document.getElementById('excelFile').value = ""
            return
          }
          listArrTemp.push(item)
        }
        this.list = listArrTemp
      }).catch()
    },
    /**
     * [doConfirmHandel 点击确定]
     * @return {[type]} [description]
     */
    doConfirmHandel() {
      let orderIdsTemp = this.list && this.list.length ? this.list : []
      let orderIds = orderIdsTemp.map(item => {
        return item.orderId
      })
      let _params = {
        opType: 4, // 操作类型 1置为过期，2修改门店 3退款 4核销 
        orderIds: orderIds
      }
      this.conDisabled = true
      this.saveLoading = true
      this.$http.post(requestUrl.dealOrder, {..._params}).then(res => {
        setTimeout(() => {
          this.conDisabled = false
          this.saveLoading = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("批量核销成功")
          this.isShow = false
          this.$emit("doSearchHandel")
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.conDisabled = false
        this.saveLoading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * @description: 批量核销本来鲜订单
     * @param {type} 
     * @return: 
     */
    doConfirmHandelBlx() {
      let orderIdsTemp = this.list && this.list.length ? this.list : []
      let orderIds = orderIdsTemp.map(item => {
        return item.orderId
      })
      let _params = {
        opType: 4, // 操作类型 1置为过期，2修改门店 3退款 4核销 
        orderIds: orderIds
      }
      this.conDisabled = true
      this.saveLoading = true
      this.$http.post(requestUrl.cancelUserRoleOrder, {..._params}).then(res => {
        setTimeout(() => {
          this.conDisabled = false
          this.saveLoading = false
        }, 800)
        if(res.code === 0) {
          this.$message.success("批量核销成功")
          this.isShow = false
          this.$emit("doSearchHandel")
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.conDisabled = false
        this.saveLoading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    } 
  }
}
</script>



